var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"home"},[_c('v-container',[_c('v-row',{staticClass:"text-center"},[_c('v-col',{attrs:{"cols":"12"}},[_c('v-img',{staticClass:"my-3",attrs:{"src":require('../assets/logo.svg'),"contain":"","height":"200"}})],1),_c('v-col',{staticClass:"mb-4"},[_c('h1',{staticClass:"display-2 font-weight-bold mb-3"},[_vm._v("Dissociation of Front-End Frameworks")])]),_c('v-col',{staticClass:"mb-5",attrs:{"cols":"12"}},[_c('h2',{staticClass:"headline font-weight-bold mb-3"},[_vm._v("What's next?")]),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',_vm._l((_vm.whatsNext),function(next,i){return _c('a',{key:i,staticClass:"subheading mx-3",attrs:{"href":next.href,"target":"_blank"}},[_vm._v(_vm._s(next.text))])}),0)],1)],1),_c('v-col',[_c('h2',{staticClass:"headline font-weight-bold mb-3"},[_vm._v("Classification in the context of the project seminar ")]),_c('p',[_vm._v("This seminar thesis is part of the project seminar „Game of Competences”, which has the objective to implement a web-based simulation platform for public administrations. Because of its complexity, the use of a front-end framework is essential. Thus, a comprehensive overview of the methodology for a successful front-end implementation using the framework Vue.js is presented here. Front-end Frameworks offer a scaffold for front-end applications including a document and folder structure, various functions, for instance CSS components, AJAX requests and much more. Firstly, an evaluation of the most popular front-end frameworks is conducted and Vue.js is weighed up against other frameworks. Secondly, the basic toolset and further functionalities of Vue.js is illustrated. Thirdly, influential design languages are clarified and finally an interactive website illustrates the wide range of Vue’s capabilities.")]),_c('h2',{staticClass:"headline font-weight-bold mb-3"},[_vm._v("Use Cases and Relevance of Front-End Frameworks")]),_c('p',[_vm._v("HTML5, CSS3 und possibly JS may be used for a simple front-end, but a complex website needs management to provide introduce order in otherwise cluttered and chaotic documents. Front-end frameworks foster „separation of concerns”, which states that each part of a program has its own purpose and act independently. This provides clarity, transparency and maintainability. A framework allows fast-tracking of the implementations because it addresses frequent issues.")]),_c('h2',{staticClass:"headline font-weight-bold mb-3"},[_vm._v("Evaluation of Vue.js, React and Angular")]),_c('p',[_vm._v("Vue.js, React and Angular attempt to solve problems differently. The biggest of the three, Angular, offers a large variety of features, whereas React does not and builds on its big community to offer for example libraries for state management like Redux. Unlike React, Vue has a comparatively smaller community. Hence, it uses a hybrid approach because it includes the frequently used features, but not as many as Angular. ")]),_c('p',[_vm._v("React is technically a library because it only renders the UI. On the one hand, it provides freedom. On the other hand, the programmer has to cope with a lot of decision-making. React and Vue use compile into powerful virtual DOM rendering functions, which are partly responsible for their great startup and runtime performance.")]),_c('p',[_vm._v("As a reflection, Vue is easier to learn than React and Angular because it forces fewer decisions onto the programmer thana React and does not involve a great number of tools to learn unlike Angular. It is specialized for an adaptable UI and complex single-page applications. Thus, in the context of the project seminar, Vue allows for the fastest acquisition of the necessary skill toolset to successfully implement a web-based simulation platform for public administrations. (Comparison with Other Frameworks — Vue.Js, n.d.; React vs Angular vs Vue.Js — What to Choose in 2020? (Updated in 2020) | by TechMagic | TechMagic | Medium, n.d.; Vue.Js | Vue vs React vs Angular | Angular vs Vue | React vs Vue, n.d.)")])])],1)],1)],1)}
